input[type="checkbox"] {
    display: none;
}
.teeth{
    cursor: pointer;
    color: white;
}
.teeth-part input:checked + label{
    color:#B832A2;
}

.add-presta{
    height: 181px;
    display:flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;
    background-color: rgba(58, 62, 74,.3);
    padding:20px;
    border-radius:10px;
    margin:20px 10px;
    border: 2px dotted rgba(58, 62, 74,1);
}

.send-btn-commande{
    background-color: rgba(58, 62, 74,1) !important;
    margin-left:10px;
}
.first-row-commande{
    background-color: rgba(58, 62, 74,1);
    padding:20px;
    border-radius: 10px;
}
.calcul-total-flux{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color:#F2FAFF;
    color:rgba(58, 62, 74,1);
    width:300px;
    border-radius:5px;
    box-shadow: 0px 3px 15px -4px rgba(0,0,0,0.7);
}

.calcul-total-flux p{
    color:rgba(58, 62, 74,1);
    margin-top:13px;
    font-size:10px;
}

.calcul-total-flux p span{
    font-size: 20px;
    color:#B832A2;
}


.icon-commande{
    margin-top:-5px;
}
.margin-neg{
    margin-bottom:3px;
}

.row-search-bar{
    background-color: rgba(58, 62, 74,1);
    padding:10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.div-search-bar{
    display:flex;
    flex-direction:column;
}

.div-search-bar label{
    color:white;
}
.mt-neg{
    margin-top:-10px;
}
p{
    font-size:12px;
}
.view-pdf{
    width:21cm;
    height:29.7cm;
    border:1px solid white;
    background-color: white;

}
.first-content,.second-content{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    margin:50px;
}

.first-content img{width:130px;margin-top:-20px;}

.second-content .destinataire{margin-right:100px;}
.second-content .destinataire p span{font-size:20px;font-weight: bold;}
.second-content .expediteur p span{font-size:16px;font-weight:bold;}

.third-content{
    text-align:center;
}
.third-content h3{
    font-size: 18px;
    font-weight: bold;
}
.flex-title-table{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgba(58, 62, 74,1);

}
.flex-title-table p{
    color:white;
    margin:10px;
}
table th {
    width: auto !important;
}
.five-content{
    margin-top:30px;
    width:50%;
}
.total-price-presta{
    display:flex;
    flex-direction:row;
    justify-content: space-between;
}
.total-price-presta.line-top{
    border-top: 2px solid black;
    font-weight: bold;
}
.total-price-presta.line-top p{
    font-size:16px;
}


/* SIX CONTENT - LE DERRIÈRE */
.declaration,.identification{
    border:2px solid black;
    padding:30px;
}
.identification{
    margin-top:30px;
}

.identifcation-detail-1{
    display:flex;
    flex-direction:row;
    justify-content: space-between;

}
.identifcation-detail-1 div{
    font-size:12px;
}
.six-content{
    background-color:white;
}
.six-content .declaration-title{
    text-align: center;
}
.six-content .declaration-title p:first-of-type{
    font-size:18px;
}
.six-content .laboratoire-detail-1{
    background-color: rgb(211, 209, 209);
    padding:20px;
    border:1px solid black;
    border-radius:10px;
}
.six-content .laboratoire-detail-1 p{
    text-align: center;
}
@media all {
    .page-break {
      display: none;
    }
  }
  
  @media print {
    html, body {
      height: auto !important;
      height: initial !important;
      overflow: visible !important;
      overflow: initial !important;
      -webkit-print-color-adjust: exact;
    }
  }
  @media print {
    .page-break {
      margin-top: 1000px;
      display: block;
      page-break-before: auto;
    }
  }
  @page {
    size: auto;
    margin: 20mm;
  }
body{
    background: rgb(242,250,255);
    background: linear-gradient(90deg, rgba(242,250,255,1) 0%, rgba(242,250,255,1) 15%, rgba(96,186,237,.3) 100%);
}
.color1{
    background-color:#3A3E4A;
    padding:0;
}
.color2{
    background-color:#F2FAFF;
    box-shadow:1px 1px 10px rgba(0, 0, 0, 0.411);
}
h1,h2,h3,h4,h5,p,a{
    font-family: Arial, Helvetica, sans-serif;
    color:#3A3E4A;
    text-decoration:none;
}




/*dashboard*/
.bloc-content{
    background-color:#F2FAFF;
    padding:40px;
    border-radius:10px;
    box-shadow:0px 3px 15px -4px rgba(0,0,0,0.7)
}
.row-content{
    padding:20px 0px;
}
.header-content{
    margin-top:30px;
}
.bloc-content h5{
    text-align:center;
}
.header-title{
    position:relative;
}
.header-content{
    position:relative;
}
.header-title::after, .header-content::after{
    position:absolute;
    content:'';
    width:300px;
    height:2px;
    background-color:rgba(184, 50, 162,1);
    top:38px;
    left:0px;
}
