
/*
bleu : #60BAED  rgba(96, 186, 237,1)
blanc 1 : #F2FAFF
blanc 2 : #FFFFFF
violet : #B832A2  rgba(184, 50, 162,1)
noir mat : #3A3E4A rgba(58, 62, 74,1);
*/

/*COULEUR*/
.bg-1{background-color:rgba(58, 62, 74,1)}
.bg-2{background-color:#F2FAFF ;}
.bg-3{background-color:rgba(96, 186, 237,1)}
.bg-4{background-color:rgba(184, 50, 162,1)}

.color-1{color:rgba(58, 62, 74,1)}
.color-2{color:#F2FAFF}
.color-3{color:rgba(96, 186, 237,1)}
.color-4{color:rgba(184, 50, 162,1)}
.color-5{color:rgba(77, 77, 77, 0.575)}/*gris*/

/*PADDING & MARGIN*/
.p-10{padding:10px;}
.m-100{margin-top:100px;}

/*FONT*/
.f-little{font-size:12px;}
.f-medium{font-size:14px;}
.line-0{line-height:0px;}

/*STRUCTURE*/
a, a:hover{text-decoration:none;}
.d-col-flex{display:flex;flex-direction:column;justify-content: space-around;}
.d-row-flex{display:flex;flex-direction:row;justify-content: space-around;}
.radius-top{border-top-left-radius: 5px;border-top-right-radius: 5px;}

/* BOUTON */
.btn-style-1,.btn-style-2,.btn-style-3{background-color: #B832A2;border:none;border-radius:5px;color:white;font-size:14px;padding:10px 10px;margin:5px;transition:all .1s ease-in-out;}
.btn-style-3{background-color: #3A3E4A;}
.btn-style-2:hover,.btn-style-3:hover{transition:all .2s ease-in-out;color:white;}
.btn-style-2:hover{background-color:#771e68;}
.btn-style-3:hover{background-color: #0a0b0e;}

/*INPUT*/
.input-style-1{background-color: transparent;border: none;border-bottom: 1px solid #B832A2;color:white;}

/*LABEL*/
.label-style-1{color:white;}

/*LISTE & TABLEAU*/
.row-list{background-color: #F2FAFF;padding:10px;border-bottom:0.5px solid grey}
.row-list:hover{background-color: rgba(58, 62, 74,0.05);}